.app {
    position: relative;
    overflow: hidden;
    height: 100vh;
    background: linear-gradient(135deg, #ff9a9e 0%, #fad0c4 100%);
    font-family: 'Arial', sans-serif;
    color: #333;
  }
  
  .exercise {
    position: relative;
    width: 100%;
    height: 100%;
  }
  
  .ball {
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .settings-icon {
    position: absolute;
    top: 10px;
    right: 10px;
    background: #ff6f61;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 10px;
    cursor: pointer;
    font-size: 18px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2);
    transition: transform 0.2s;
  }
  
  .settings-icon:hover {
    transform: scale(1.1);
    background: #e85a4f;
  }
  
  .settings-popup {
    position: absolute;
    top: 50px;
    right: 10px;
    padding: 20px;
    background: white;
    border: 2px solid #ff6f61;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    animation: fadeIn 0.3s ease-in-out;
  }
  
  .settings-popup h3 {
    margin: 0 0 10px;
    font-size: 1.2rem;
  }
  
  .settings-popup label {
    display: block;
    margin-bottom: 15px;
  }
  
  .settings-popup .ball-counter {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .settings-popup button {
    padding: 8px 12px;
    background: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.2s;
  }
  
  .settings-popup button:hover {
    background: #0056b3;
  }
  
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  